




























































































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			buildinf:{},
			vehiclelist:[],
			carrylist:[],
			name:'',
			date:'',
			time:'',
			y:"",
			m:'',
			d:'',
			adress:{},
			vehicleindex:0,
			carryindex:0,
			allprice:'',
			mallprice:'',
			sendprice:'',
			carryprice:'',
			remark:'',
			requireCarry:1,
			floor:'',
			static:''
		}
	},
	onLoad(opt) {
		this.static=ut.static;
		this.name=opt.name;
		this.buildinf=wx.getStorageSync('buildinf');
		this.buildinf.forEach(item=>{
			if(item.picture)item.picture=item.picture.split(',')[0];
		});
		wx.setStorageSync('buildinf','');
		this.req_vehiclelist();
		this.req_carrylist();
		this.req_getdefaddress();
		this.buildinf.forEach(item=>{
			this.mallprice+=item.price;
			item.goodsSpecId=item.id;
			item.goodsNumber=item.num;
		})
	},
	onShow(){
		if(wx.getStorageSync('adress')){
			this.adress=wx.getStorageSync('adress');
			wx.setStorageSync('adress','');
		}
	},
	methods: {
		cg_requireCarry(type){
			this.requireCarry=type;
		},
		getallprice(){
			this.allprice=(Number(this.mallprice)+Number(this.sendprice)+Number(this.carryprice)).toFixed(2);
		},
		cg_vehicleindex(index){
			this.vehicleindex=index;
			this.sendprice=this.vehiclelist[index].startPrice;
			this.getallprice();
		},
		cg_carryindex(index){
			this.carryindex=index;
			this.carryprice=this.carrylist[index].startPrice;
			this.getallprice();
		},
		go_mine_adress(){
			wx.navigateTo({
				url: '../mine/adress?src=pay'
			})
		},
		timeChange: function (e) {
			this.time=e.detail.value;
		},
		dateChange: function (e) {
			this.date=e.detail.value;
			const date=this.date.split('-');
			this.y=date[0];
			this.m=date[1];
			this.d=date[2];
		},
		req_unifiedOrder(){
			if (!this.adress) {
				ut.totast('请选择地址');
				return;
			}
			if(!this.date){
				ut.totast('请选择日期');
				return;
			}
			if(!this.time){
				ut.totast('请选择时间');
				return;
			}
			if(!this.floor){
				ut.totast('请选择楼层');
				return;
			}
			ut.request({
				data: {
					 "addressId": this.adress.id,
					  "carryTypeId": this.carrylist[this.carryindex].id,
						"expressTypeId": this.vehiclelist[this.vehicleindex].id,
						"expressTime": this.date+this.time,
						"floor": this.floor,//楼层
						"orderGoods": this.buildinf,
						"remark": this.remark,
						 "number": 1,
						"requireCarry": this.requireCarry//是否搬运1是2否
				},
				url: "goods/order/unifiedOrder",
				c:true
			}).then(data=>{
				ut.totast('下单成功')
			})
		},
		req_vehiclelist(){
			ut.request({
				url: "order/vehiclelist"
			}).then(data=>{
				this.vehiclelist=data;
				this.sendprice=this.vehiclelist[0].startPrice;
				this.getallprice();
			})
		},
		req_carrylist(){
			ut.request({
				url: "order/carrylist"
			}).then(data=>{
				this.carrylist=data;
				this.carryprice=this.carrylist[0].startPrice;
				this.getallprice();
			})
		},		
		req_getdefaddress(){
			ut.request({
				url: "address/getdefaddress"
			}).then(data=>{
				this.adress=data;
			})
		}
	}
}
